<template>
  <v-container>
    <div>
      <div class="bot">
        <h2>Editing File: {{ fileName }}<v-tooltip><v-btn icon @click="open('fileManage')"> arrow-left-bottom-bold</v-btn></v-tooltip></h2>
      </div>
      <mavon-editor
        v-model="value"
        ref="md"
        @change="change"
        @imgAdd="$imgAdd"
        @save="save"
        style="min-height: 560px; max-height: 570px"
        :toolbars="markdownOption"
        :shortCut="false"
        id="editor"
      >
      </mavon-editor>
      <!--错误提示，用于反馈创建、修改等等操作时的错误-->

      <v-snackbar v-model="snackbar1" top>
        {{ snackbar1Data }}
        You will jump to FilePage in {{ count }} second!
        <template v-slot:action="{ attrs }">
          <v-btn color="red" text v-bind="attrs" @click="snackbar1 = false">
            Close
          </v-btn>
        </template>
      </v-snackbar>

      <v-snackbar v-model="snackbar2">
        {{ snackbar2Data }}
        <template v-slot:action="{ attrs }">
          <v-btn color="red" text v-bind="attrs" @click="snackbar2 = false">
            Close
          </v-btn>
        </template>
      </v-snackbar>
    </div>
  </v-container>
</template>

<script type="text/ecmascript-6">
// 导入组件 及 组件样式

import { mavonEditor } from "mavon-editor";
import "mavon-editor/dist/css/index.css";

export default {
  name: "Markdown",
  components: {
    mavonEditor, //mavon-editor组件
  },
  data() {
    return {
      lastValue: "", // 上一次保存前的content

      html: "", // 转成的html
      toolBg: "rgba(255,255,0)",
      preBg: "white",
      //未创建文件，跳转提示框(用于进行倒计时)
      snackbar1: false,
      snackbar1Data: "",
      //提示框，可以提示各种消息
      snackbar2: false,
      snackbar2Data: "",
      count: 5,

      markdownOption: {
        bold: true, // 粗体
        italic: true, // 斜体
        header: true, // 标题
        underline: true, // 下划线
        strikethrough: true, // 中划线
        mark: true, // 标记
        superscript: true, // 上角标
        subscript: true, // 下角标
        quote: true, // 引用
        ol: true, // 有序列表
        ul: true, // 无序列表
        link: true, // 链接
        imagelink: true, // 图片链接
        code: true, // code
        table: true, // 表格
        fullscreen: true, // 全屏编辑
        readmodel: false, // 沉浸式阅读
        htmlcode: true, // 展示html源码
        help: true, // 帮助
        /* 1.3.5 */
        undo: false, // 上一步
        redo: false, // 下一步
        trash: true, // 清空
        save: true, // 保存（触发events中的save事件）
        /* 1.4.2 */
        navigation: false, // 导航目录
        /* 2.1.8 */
        alignleft: true, // 左对齐
        aligncenter: true, // 居中
        alignright: true, // 右对齐
        /* 2.2.1 */
        subfield: true, // 单双栏模式
        preview: true, // 预览
      },
    };
  },
  computed: {
    userName() {
      return this.$store.state.user.username;
    },
    value: {
      get() {
        return this.$store.state.file.editingFile;
      },
      set(val) {
        this.$store.commit("setEditingFile", val);
      },
    },
    fileName: {
      get() {
        return this.$store.state.file.editingFileName;
      },
      set(val) {
       
      },
    },
    fileUserType: {
      get() {
        return this.$store.state.file.editingFileUserType;
      },
      set(val) {
        
      },
    },
    filePath: {
      get() {
        return this.$store.state.file.editingFilePath;
      },
      set(val) {
        
      },
    },
    fileEditor:{
      get() {
        return this.$store.state.file.editingFileEditor;
      },
      set(val) {
        
      },
    }
  },
  created() {
    this.lastValue = this.value;
  },

  methods: {
    change(value, render) {
      //实时获取转成html的数据
      this.html = render;
    },
    // 将图片上传到服务器，返回地址替换到md中
    // 绑定@imgAdd event
    $imgAdd(pos, file) {
      //对图片格式进行过滤
      const isJPG =
        file.type === "image/jpeg" ||
        file.type === "image/png" ||
        file.type === "image/jpg" ||
        file.type === "image/gif";
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isJPG) {
        this.snackbar2Data = "Wrong FileType!";
        this.snackbar2 = true;
      }
      if (!isLt2M) {
        this.snackbar2Data = "Please Upload a file under 2M!";
        this.snackbar2 = true;
      }
      if (isJPG && isLt2M) {
        console.log("checked picture");
        var formData = new FormData();
        formData.append("image", file);
        formData.append("username", this.userName);

        this.axios({
          url: this.targetIP() + "/fileManage.ImageUp",
          method: "post",
          data: formData,
          headers: {
            "Content-Type": "multipart/form-data",
          },
        }).then((url) => {
          console.log(url.data.msg);
          if (url.data.status == 1) {
            this.snackbar2Data = "picture uploaded success!";
            this.snackbar2 = true;
            this.$refs.md.$img2Url(pos, url.data.imgUrl);
          } else {
            this.snackbar2Data = "picture uploaded wrong!";
            this.snackbar2 = true;
          }
        });
      }
      this.save();
    },
    save() {
      console.log("save");
      let deleteImgChart = this.findDeleteImg();

      if (deleteImgChart.length != 0) {
        console.log(deleteImgChart);

        this.axios
          .post(this.targetIP() + "/fileManage.ImageUp/delete", {
            imageArr: deleteImgChart,
          })
          .then((url) => {
            console.log(url.data);
            if (url.data == true) {
              this.snackbar2Data = "picture refresh success!";
              this.snackbar2 = true;
            } else {
              this.snackbar2Data = "picture refresh wrong!";
              this.snackbar2 = true;
            }
          });
      } else {
        console.log("no pic");
      }

      this.updateFile();
      this.lastValue = this.value; //上一次的赋值
    },
    findDeleteImg() {
      let pattern = /(?<=!\[.*\]\(http:\/\/[\w.\b]*\/FileImage\/)[\w\/.]*/g;
      let lastArr = this.lastValue.match(pattern);
      if (lastArr != null) {
        let deleteArr = [];
        for (var i = 0; i <= lastArr.length; i++) {
          if (this.value.search(lastArr[i]) == -1) {
            deleteArr.push(lastArr[i]);
          }
        }
        return deleteArr;
      } else {
        return [];
      }
    },
    updateFile() {
      
      this.axios
        .post(this.targetIP() + "/fileManage.FileManage/updateContent", {
          userName: this.fileEditor,
          fileName: this.fileName,
          fileType: "FILE",
          userType: this.fileUserType,
          filePath: this.filePath,
          newContent: this.value,
        })
        .then((response) => {
          console.log(response);
          if (response.data == false) {
            this.snackbar1Data = "You have to Create the file First!";
            this.snackbar1 = true;
            this.countdown();
          }
          if (response.data == true) {
            this.snackbar2Data = "Saved!";
            this.snackbar2 = true;
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },
    targetIP() {
      return this.$store.state.targetIP;
    },
    open(name) {
      // 需要先判断当前路由名称是否和目标路由一致，如果一致就不跳转
      if (this.$route.name !== name) {
        this.$router.push({
          name: name,
        });
      }
    },
    //倒计时
    countdown() {
      const TIME_COUNT = 3;
      if (!this.timer) {
        this.count = TIME_COUNT;
        this.show = false;
        this.timer = setInterval(() => {
          if (this.count > 0 && this.count <= TIME_COUNT) {
            this.count--;
          } else {
            this.show = true;
            clearInterval(this.timer);
            this.timer = null;
            //跳转的页面写在此处
            this.$router.push({
              name: "fileManage",
            });
          }
        }, 1000);
      }
    },
  },
};
</script>
<style>
.bot {
  z-index: -1;
}
.v-note-wrapper {
  position: static !important;
}
</style>
